const statusInitialState = {
  status: {
    isOpen: false,
  },
  update: {
    isUpdate: false,
  },
};

const statusActions = {
  open: (state) => {
    return { status: { isOpen: true } };
  },
  close: (state) => {
    return { status: { isOpen: false } };
  },
  updateTodo: (state) => {
    return { update: { isUpdate: true } };
  },
  addTodo: (state) => {
    return { update: { isUpdate: false } };
  },
};

export { statusInitialState, statusActions };
