import React, { useState, useEffect, useCallback } from "react";
import { useStore } from "../store/useStore";
import { addNewTodo, getTodoCategory, updateTodo } from "./servise";
import "./../assets/scss/App.scss";
import { TextField, Button, Dialog, DialogActions } from "@material-ui/core/";
import { makeStyles } from "@material-ui/core/styles";
import Autocomplete, {
  createFilterOptions,
} from "@material-ui/lab/Autocomplete";

const filter = createFilterOptions();

const useStyles = makeStyles((theme) => ({
  root: {
    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: 200,
    },
  },
}));

function TodoForm(props) {
  let todoData = props.todoData;
  const classes = useStyles();
  const [value, setValue] = useState(null);
  const [categoryList, setCategoryList] = useState([]);
  const [todoName, setTodoName] = useState("");
  const [todoTime, setTodoTime] = useState("");
  const [todoDescription, setTodoDescription] = useState("");

  const { state, dispatch } = useStore();
  const close = useCallback(() => dispatch({ type: "close" }), [dispatch]);
  const addTodo = useCallback(() => dispatch({ type: "addTodo" }), [dispatch]);

  useEffect(async () => {
    setCategoryList(await getTodoCategory());
  }, []);

  useEffect(async () => {
    if (state.update.isUpdate) {
      setTodoName(todoData.name);
      setTodoTime(todoData.date);
      setTodoDescription(todoData.description);
      setValue(todoData.category);
    }
    if (!state.update.isUpdate) {
      setTodoName("");
      setTodoTime("");
      setTodoDescription("");
      setValue(null);
    }
  }, [state.update.isUpdate]);

  function handleInputName(e) {
    setTodoName(e.target.value);
  }

  function handleInputDescription(e) {
    setTodoDescription(e.target.value);
  }

  function handleInputTime(e) {
    setTodoTime(e.target.value);
  }

  function handleAddTodo() {
    const newTodo = {
      name: todoName,
      description: todoDescription,
      date: todoTime,
      category: value,
    };
    console.log("todoData", todoData);
    state.update.isUpdate
      ? updateTodo(todoData._id, newTodo)
      : addNewTodo(JSON.stringify(newTodo));
    setValue("");
    setTodoName("");
    setTodoTime("");
    setTodoDescription("");
    close();
    addTodo();
  }

  const handleClose = () => {
    close();
    addTodo();
  };

  return (
    <>
      <Dialog
        open={state.status.isOpen}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <form className={classes.root} noValidate autoComplete="off">
          <div>
            <TextField
              required
              id="standard-required"
              label="Name"
              value={todoName}
              onChange={(e) => handleInputName(e)}
            />
            <TextField
              id="datetime-local"
              label="Select Date"
              type="datetime-local"
              value={todoTime}
              onChange={(e) => handleInputTime(e)}
              InputLabelProps={{
                shrink: true,
              }}
            />
          </div>
          <Autocomplete
            value={value}
            onChange={(event, newValue) => {
              if (typeof newValue === "string") {
                setValue(newValue);
              } else if (newValue) {
                // Create a new value from the user input
                setValue(newValue);
              } else {
                setValue(newValue);
              }
            }}
            filterOptions={(options, params) => {
              const filtered = filter(options, params);
              // Suggest the creation of a new value
              if (params.inputValue !== "") {
                filtered.push(params.inputValue);
              }
              return filtered;
            }}
            selectOnFocus
            clearOnBlur
            handleHomeEndKeys
            id="free-solo-with-text-demo"
            options={categoryList}
            getOptionLabel={(option) => {
              // Value selected with enter, right from the input

              if (typeof option === "string") {
                return option;
              }
              // Add "xxx" option created dynamicall
              if (option) {
                return option;
              }
              // Regular option
              return option;
            }}
            renderOption={(option) => option}
            style={{ width: 300 }}
            freeSolo
            renderInput={(params) => (
              <TextField {...params} label="Add category" />
            )}
          />
          <TextField
            style={{ margin: 8, width: 410 }}
            id="standard-full-width"
            label="Description"
            placeholder="Enter Description"
            margin="normal"
            value={todoDescription}
            onChange={(e) => handleInputDescription(e)}
            InputLabelProps={{
              shrink: true,
            }}
          />
        </form>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button
            variant="contained"
            color="primary"
            className="formButtom"
            onClick={handleAddTodo}
          >
            Add todo
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}

export default TodoForm;
